.ant-menu-item-selected {
    background-color: rgba(0, 0, 0, 0) !important;
}

.ant-menu-item {
    color: #ffffff !important;
}

.sideMenu {
    padding: 10px;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-content: space-between;
    justify-content: center;
}

.sideMenu button {
    border: none;
}

.menuItem {
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    line-height: 37px;
    margin-left: 22px;
}

.menuItem:hover {
    color: #0EC261 !important;
}

.socialItems {
    margin: 0 25%;
}

.langChanger {
    background-color: #0EC261;
    border: none;
    color: white;
}